/**
 * Hook para Deteco de Dispositivos Mveis
 *
 * Custom hook que detecta se o usurio est em um dispositivo mvel
 * baseado na biblioteca rainersoft/utils.
 *
 * @module @rainersoft/ui/hooks
 * @author Rainer Teixeira
 */

'use client';

import * as React from 'react';
import { isMobile } from '../dom';

/**
 * Hook useIsMobile
 *
 * Detecta se o viewport atual  considerado mobile usando rainersoft/utils.
 * Wrapper reativo para a funo isMobile().
 *
 * @returns {boolean} true se a viewport  mobile, false caso contrrio
 *
 * @example
 * import { useIsMobile } from '@rainersoft/ui'
 *
 * function MyComponent() {
 *   const isMobile = useIsMobile()
 *   return <div>{isMobile ? <MobileMenu /> : <DesktopMenu />}</div>
 * }
 */
export function useIsMobile() {
  const [isMobileState, setIsMobileState] = React.useState<boolean>(false);

  React.useEffect(() => {
    setIsMobileState(isMobile());
  }, []);

  return isMobileState;
}

