/**
 * Helpers pt-BR - Funes pr-configuradas para portugus brasileiro
 * 
 * Este mdulo fornece wrappers convenientes para funes de formatao
 * com o locale 'pt-BR' pr-configurado, eliminando a necessidade de
 * passar o parmetro locale repetidamente.
 * 
 * Ideal para projetos exclusivamente em portugus brasileiro.
 * 
 * @module @rainersoft/utils/pt-br
 * @author Rainer Teixeira
 * @version 1.0.0
 * @since 1.0.0
 */

import {
  formatDate as formatDateBase,
  formatDateTime as formatDateTimeBase,
  formatRelativeDate as formatRelativeDateBase,
} from '../date';

import {
  formatCurrency as formatCurrencyBase,
  formatNumber as formatNumberBase,
  formatCompact as formatCompactBase,
} from '../number';

import {
  translateStatus as translateStatusBase,
} from '../status';

/**
 * Formata uma data em portugus brasileiro (pt-BR)
 * 
 * @param date - Data a ser formatada (string ou objeto Date)
 * @param format - Formato desejado: 'short', 'long' ou 'full' (padro: 'long')
 * @returns Data formatada em portugus brasileiro
 * 
 * @example
 * formatDate(new Date()); // '26 de novembro de 2025'
 * formatDate('2024-01-15', 'short'); // '15/01/2024'
 */
export function formatDate(
  date: string | Date,
  format: 'short' | 'long' | 'full' = 'long'
): string {
  return formatDateBase(date, format, 'pt-BR');
}

/**
 * Formata data e hora em portugus brasileiro (pt-BR)
 * 
 * @param date - Data com hora a ser formatada
 * @returns Data e hora formatadas em portugus brasileiro
 * 
 * @example
 * formatDateTime(new Date()); // '26 de novembro de 2025 s 14:30'
 */
export function formatDateTime(date: string | Date): string {
  return formatDateTimeBase(date, 'pt-BR');
}

/**
 * Formata uma data relativa em portugus brasileiro
 * 
 * Exibe o tempo relativo (ex: "h 2 dias", "h 1 ms")
 * 
 * @param date - Data de referncia
 * @returns Data relativa formatada em portugus
 * 
 * @example
 * formatRelativeDate(new Date(Date.now() - 86400000)); // 'h 1 dia'
 */
export function formatRelativeDate(date: string | Date): string {
  return formatRelativeDateBase(date, 'pt-BR');
}

/**
 * Formata um valor monetrio em Real brasileiro (BRL)
 * 
 * @param value - Valor numrico a ser formatado
 * @param options - Opes adicionais de formatao (Intl.NumberFormatOptions)
 * @returns Valor formatado como moeda brasileira
 * 
 * @example
 * formatCurrency(1234.56); // 'R$ 1.234,56'
 * formatCurrency(99.99, { minimumFractionDigits: 2 }); // 'R$ 99,99'
 */
export function formatCurrency(
  value: number,
  options?: Intl.NumberFormatOptions
): string {
  return formatCurrencyBase(value, 'pt-BR', options);
}

/**
 * Formata um nmero em portugus brasileiro
 * 
 * @param value - Nmero a ser formatado
 * @param decimals - Nmero de casas decimais (padro: 0)
 * @returns Nmero formatado com separadores brasileiros
 * 
 * @example
 * formatNumber(1234567); // '1.234.567'
 * formatNumber(1234.567, 2); // '1.234,57'
 */
export function formatNumber(value: number, decimals = 0): string {
  return formatNumberBase(value, decimals, 'pt-BR');
}

/**
 * Formata um nmero de forma compacta em portugus brasileiro
 * 
 * Usa sufixos como "mil", "mi", "bi" para nmeros grandes
 * 
 * @param value - Nmero a ser formatado
 * @param decimals - Casas decimais para nmeros compactos (padro: 1)
 * @returns Nmero formatado de forma compacta
 * 
 * @example
 * formatCompact(1500); // '1,5 mil'
 * formatCompact(2500000); // '2,5 mi'
 */
export function formatCompact(value: number, decimals = 1): string {
  return formatCompactBase(value, decimals, 'pt-BR');
}

/**
 * Traduz um cdigo de status para portugus brasileiro
 * 
 * @param status - Cdigo do status (ex: 'DRAFT', 'PUBLISHED', 'ACTIVE')
 * @returns Status traduzido para portugus brasileiro
 * 
 * @example
 * translateStatus('DRAFT'); // 'Rascunho'
 * translateStatus('PUBLISHED'); // 'Publicado'
 */
export function translateStatus(status: string): string {
  return translateStatusBase(status, 'pt-BR');
}

// Exportao padro como objeto para facilitar a importao
export default {
  formatDate,
  formatDateTime,
  formatRelativeDate,
  formatCurrency,
  formatNumber,
  formatCompact,
  translateStatus,
};