'use client';

import { useEffect, useRef, useState } from 'react';

/**
 * Opes para o hook {@link useIntersectionObserver}.
 */
interface UseIntersectionObserverOptions {
  /** Threshold nico ou lista para disparo do observer. */
  threshold?: number | number[];
  /** Margem aplicada ao root para clculo de interseo. */
  rootMargin?: string;
  /** Elemento raiz para observao (default: viewport). */
  root?: Element | null;
  /** Paralisa o observer aps primeira visibilidade. */
  freezeOnceVisible?: boolean;
}

/**
 * Resultado retornado pelo hook {@link useIntersectionObserver}.
 */
interface UseIntersectionObserverResult {
  /** Informa se o elemento est em interseo. */
  isIntersecting: boolean;
  /** Ref a ser atribuda ao elemento observado. */
  targetRef: React.RefObject<Element | null>;
  /** ltima entrada capturada pelo IntersectionObserver. */
  entry?: IntersectionObserverEntry;
}

/**
 * Hook para usar Intersection Observer de forma reutilizvel
 * 
 * @param options - Opes do Intersection Observer
 * @returns Objeto com estado de interseo e ref para o elemento
 */
export function useIntersectionObserver(
  options: UseIntersectionObserverOptions = {}
): UseIntersectionObserverResult {
  const {
    threshold = 0,
    rootMargin = '0px',
    root = null,
    freezeOnceVisible = false
  } = options;

  const [isIntersecting, setIsIntersecting] = useState(false);
  const [entry, setEntry] = useState<IntersectionObserverEntry>();
  const targetRef = useRef<Element | null>(null);

  useEffect(() => {
    const target = targetRef.current;
    if (!target) return;

    const hasSupport = 'IntersectionObserver' in window;
    if (!hasSupport) return;

    const observer = new IntersectionObserver(
      ([entry]) => {
        const isElementIntersecting = entry.isIntersecting;

        setEntry(entry);
        setIsIntersecting(isElementIntersecting);

        if (freezeOnceVisible && isElementIntersecting) {
          observer.disconnect();
        }
      },
      {
        threshold,
        rootMargin,
        root
      }
    );

    observer.observe(target);

    return () => {
      observer.disconnect();
    };
  }, [
    threshold,
    rootMargin,
    root,
    freezeOnceVisible,
    targetRef.current
  ]);

  return {
    isIntersecting,
    targetRef,
    entry
  };
}
